export default {
	name:'comment-list',
	props:{
		item: Object,
		index: Number
	},
	methods:{
		openSpace(){
			uni.navigateTo({
				url: '/pages/user-space/user-space?userid='+this.item.userid
			})
		},
		reply() {
			this.$emit('reply',this.item.id);
		},
	}
}
